.contents_wrap {
  padding-top: 0 !important;
}

.focus_wrap {
  background: url(/assets/dig/images/etc/bg_focus_visual.png) no-repeat center top;
}

.focus_visual {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  width: 1200px;
  height: 400px;
}

.focus_visual span {
  font-size: 24px;
  color: #595959;
}

.focus_visual strong {
  margin-top: 10px;
  font-size: 60px;
  color: #000;
}

.focus_tab_menu {
  position: relative;
  background-color: #8783a7;
}

.focus_tab_menu .swiper-container {
  margin: 0 auto;
  width: 1200px;
}

.focus_tab_menu .swiper-button-next,
.focus_tab_menu .swiper-button-prev {
  background-size: 50px 60px;
  background-color: unset;
}

.focus_tab_menu .swiper-button-prev {
  left: calc(50% - 664px);
}

.focus_tab_menu .swiper-button-next {
  right: calc(50% - 664px);
}

.focus_conts {
  padding: 100px 0 70px;
  background: #F9F5FC url(/assets/dig/images/etc/bg_focus_conts.png) no-repeat center top;
}

.focus_conts .focus_conts_header {
  margin: 0 auto;
  width: 1200px;
  text-align: center;
}

.focus_conts .focus_conts_header h4 {
  font-size: 42px;
  color: #000;
  line-height: 60px;
}

.focus_conts .focus_conts_header p {
  position: relative;
  display: inline-block;
  margin-top: 13px;
  font-size: 20px;
  color: #000;
}

.focus_conts .focus_conts_header p:before {
  position: absolute;
  left: 0;
  bottom: -3px;
  display: block;
  width: 100%;
  height: 16px;
  background-color: rgba(80, 85, 177, 0.1);
  z-index: 0;
  content: "";
}

.focus_conts .focus_conts_item {
  display: flex;
  justify-content: space-between;
  margin: 130px auto 0;
  padding-bottom: 40px;
  width: 1200px;
  background: url(/assets/dig/images/etc/bg_focus_dot.png) no-repeat right 180px bottom;
}

.focus_conts .focus_conts_item .menu li {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}

.focus_conts .focus_conts_item .menu li strong {
  font-size: 28px;
  line-height: 40px;
  color: #767676;
}

.focus_conts .focus_conts_item .menu li p {
  margin-top: 6px;
  font-size: 16px;
  color: #767676;
}

.focus_conts .focus_conts_item .menu li:hover strong,
.focus_conts .focus_conts_item .menu li:hover p {
  color: #000;
}

.focus_conts .focus_conts_item .menu li.current strong,
.focus_conts .focus_conts_item .menu li.current p {
  color: #000;
}

.focus_conts .focus_conts_item .menu li.current:after {
  position: absolute;
  left: 0;
  top: 20px;
  display: block;
  width: 600px;
  height: 1px;
  background-color: #000;
  transform: translateX(-100%);
  content: "";
}

.focus_conts .focus_conts_item .menu li + li {
  margin-top: 30px;
}

.focus_conts .focus_conts_item .img {
  padding: 20px;
  width: 670px;
  height: 485px;
  box-sizing: border-box;
  border: 1px solid #efebe9;
  background-color: #fff;
  cursor: url(/assets/dig/images/common/cursor_mouse.png), pointer;
}

.focus_conts .focus_conts_item .img .swiper-slide {
  width: 100%;
  height: 100%;
}

.focus_conts .focus_conts_item .img .swiper-slide:empty {
  background-color: #f5f5f5;
}

.focus_conts .focus_conts_item .swiper-pagination {
  display: flex;
  flex-direction: column;
  gap: 10px 0;
  width: 10px;
  transform: translate3d(1157px, 208px, 0);
}

.focus_conts .focus_conts_item .swiper-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: #ccc;
}

.focus_conts .focus_conts_item .swiper-pagination .swiper-pagination-bullet-active {
  background-color: #595959;
}

.focus_conts .focus_conts_tip {
  margin: 100px auto 0;
  padding: 20px 18px;
  width: 1200px;
  border: 1px solid #d5d5d5;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.5);
}

.focus_conts .focus_conts_tip dt {
  margin-bottom: 10px;
  padding-left: 20px;
  font-size: 14px;
  color: #000;
  line-height: 22px;
  background: url(/assets/common/images/ico_title_message.png) no-repeat 0 center;
}

.focus_conts .focus_conts_tip dd {
  position: relative;
  padding-left: 9px;
  font-size: 13px;
  color: #595959;
  line-height: 20px;
}

.focus_conts .focus_conts_tip dd + dd {
  margin-top: 6px;
}

.focus_conts .focus_conts_tip :before {
  position: absolute;
  left: 0;
  top: 8px;
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #999;
  content: "";
}

.focus_conts .focus_conts_banner {
  margin: 100px auto 0;
  width: 1200px;
}

.focus_download {
  display: flex;
  margin: 100px auto 0;
  padding: 36px 30px 40px;
  width: 1200px;
  border-radius: 20px;
  background-color: #f7f7f7;
}